@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);

.TaskInput{position:relative}.TaskInput>input{background-color:#343a40;border:1px solid #2f343a;border-radius:.25rem;box-sizing:border-box;color:#d8dadb;padding:.7rem;padding-left:2.5rem;padding-right:6rem;width:100%}.TaskInput>input>::-webkit-input-placeholder{color:#858c95;opacity:1}.TaskInput>input>:-ms-input-placeholder{color:#858c95;opacity:1}.TaskInput>input>::placeholder{color:#858c95;opacity:1}.TaskInput>input:focus{box-shadow:0 0 0 2pt #f06292}.TaskInput>.icon{left:.9rem;top:.7rem;position:absolute;color:#858c95}.TaskInput>.key-icons{right:.6rem;top:.5rem;position:absolute}.TaskInput>.key-icons>.TdKeyIcon{margin-left:.5rem}
.TdKeyIcon{display:inline-block;border-radius:.2rem;border:1px solid #858c95;color:#858c95;cursor:pointer}.TdKeyIcon:active{border-color:#2f343a}.TdKeyIcon>.container{border-bottom:2px solid #858c95;font-size:.9rem;padding:.2rem;border-radius:.2rem}.TdKeyIcon>.container:active{background-color:#2f343a;border-color:#2f343a}
.AlertArea{box-sizing:border-box;color:#d8dadb;height:2rem;padding:1rem;position:absolute;width:100%;z-index:2;pointer-events:none}.AlertArea>div{pointer-events:visible;background-color:#2f343a;border-radius:.5rem;box-sizing:border-box;padding:1rem;margin-bottom:1rem;width:100%}
.TaskItem{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:0;position:relative;padding:.25rem}.TaskItem:hover{background-color:#343a40}.TaskItem>.content{-webkit-flex-grow:1;flex-grow:1;padding:.1rem 1rem;cursor:default}
.TdCheck{color:#d8dadb;height:1em;width:1em;border-radius:5%;border:.1em solid #2f343a;background-color:#484f59;cursor:pointer;min-width:1em;padding:.3em}
.TaskItemMenu{-webkit-flex-grow:0;flex-grow:0;min-width:2.5rem;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;position:absolute;right:0;top:0rem;background-color:#343a40;padding:.4rem 1rem .3rem 1rem}.TaskItemMenu>.menu-icon{cursor:pointer;display:none}.TaskItemMenu>.menu-icon.show{display:block}.TaskItemMenu.dialog{min-width:8rem;cursor:default}.TaskItemMenu.dialog>.no,.TaskItemMenu.dialog>.yes{cursor:pointer;font-size:1.1em}
.TaskList{position:relative}.TaskList>ul{padding:0}
.Color{background-color:#fff;box-sizing:border-box;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between;padding:1rem;width:100%}.Color .color{padding:1rem}.Color .color::before{content:"";display:block;height:100px;margin-bottom:.5rem;min-width:200px}.Color .black::before{background-color:#2f343a}.Color .charcoal::before{background-color:#343a40}.Color .anchor::before{background-color:#3c424a}.Color .smoke::before{background-color:#858c95}.Color .white::before{background-color:#d8dadb}
.Clock{text-align:center;font-size:2rem;margin-bottom:.3rem}
.Header{display:-webkit-flex;display:flex;margin-bottom:1rem}.Header>h1{-webkit-flex:2 1;flex:2 1;font-size:4rem;margin:0}.Header>aside{-webkit-flex:1 1;flex:1 1}.Header>aside>.label{text-align:center;font-size:2rem;height:32px;margin-bottom:.3rem}
.HeaderMenu{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}.HeaderMenu>.menu-icon{font-size:1.5rem;cursor:pointer}
.Body{background-color:#3c424a;border-radius:.5rem;box-shadow:0 0 30px rgba(0,0,0,.5);color:#d8dadb;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:2rem}
